.Side-menu {
    position: fixed;
    top: 50%;
    /* Vertically center the menu */
    /* left: 10px; */
    /* Position it on the left side */
    transform: translateY(-50%);
    /* Center vertically by shifting up half of its own height */
    width: 160px;
    /* Adjust width as needed */
    background-color: #f0f0f0;
    /* Light background color */
    color: #4F67A6;
    /* Default text color */
    padding: 20px;
    box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
    overflow-y: auto;
    /* Add scroll if content overflows */
    border-radius: 8px;
    /* Optional: Rounded corners */
    text-align: center;
}

.Side-menu h2 {
    font-size: 21px;
}

@media screen and (max-width: 1200px) {
    .Side-menu {
        display: none;
    }
}

.Side-menu ul {
    list-style: none;
    width: 100%;
    display: block;
    /* text-align: center; */
    /* Center the content inside the ul */
}

.Side-menu li {
    /* width: 100%; */
    margin-bottom: 7px;
    display: block;
    text-align: center;
}

.Side-menu span {
    color: #4F67A6;
    text-decoration: underline;
    font-size: 15px;
    display: block;
    margin-bottom: 10px;
    padding: 5px 0px;
    /* border-radius: 5px; */
    transition: background-color 0.3s;
    cursor: pointer;
    font-weight: bold;
}

.Side-menu span:hover {
    background-color: #c0c0c0;
    color: rgb(65, 123, 218);
}

